import React from 'react';

import './collection-item.styles.scss';

import {connect} from 'react-redux';
import CustomButton from '../custom-button/custom-button.component.jsx';
import {selectCurrentUser} from '../../redux/user/user.selector';
import {createStructuredSelector} from 'reselect';
import Grid from '@material-ui/core/Grid';
import {auth, deleteItem,UpdateList, completedItem,updateItem} from '../../firebase/firebase.utils.js';

import {setCurrentUser} from '../../redux/user/user.actions';
import {setCurrentList} from '../../redux/list/list.actions';
import {selectCurrentList} from'../../redux/list/list.selector';



const handleButtons = async(item,button)=>{
  try{
      if(auth.currentUser){
      var userAuth = auth.currentUser;
      switch(button){
        case "deleteItem":
          await deleteItem(userAuth,item);
          break;
        case "updateItem":
          await updateItem(userAuth,item);
          break;
        case "completeItem":
          await completedItem(userAuth,item);
          break;
        case "Incomplete":
          await updateItem(userAuth,item);
          break;
        default: return false;
  
      }
      await UpdateList(userAuth).then((r)=> {
          setCurrentUser({id:r});
          setCurrentList({id:r});
      });
    }else{
      switch(button){
        case "completeItem":
          await completedItem(0,item);
          break;
          default: return false;
      }
      await UpdateList(userAuth).then((r)=> {
          setCurrentUser({id:r});
          setCurrentList({id:r});
      });
    }
    
  }catch(error){
    console.error(error);
  }
}

const CollectionItem = ( { item,currentUser,completed }) => {
    return(
    
    <div className={` ${completed? completed:''} collection-item`}>
        <div className='collection-footer'>
        <Grid item xs={6}>
              {
                item?
                
                // <div className='title'>{item.title}</div>
                <div className='name'><h3>
                  {item.text}
                  </h3>
                  </div>
                // <div className="due_date">{item.date}</div>
                
                :
              
                ''
              }
        </Grid>
        <Grid item sm={6}>
          <div className="inline-buttons" >
            
            {
              currentUser?
              <div>
              <CustomButton danger onClick={()=>handleButtons(item,"deleteItem")} >Delete</CustomButton>
              <CustomButton danger onClick={()=>handleButtons(item,"Incomplete")} >Incomplete</CustomButton>
              </div>
              :''
            }
          
            {
              item.completed?
              // <h3>Vamos ter esse!</h3>
              <h3></h3>
              :
              <CustomButton success onClick={()=>handleButtons(item,"completeItem")} >Escolher</CustomButton>
            }
            
            
          </div>
        </Grid> 
        </div>
    </div>
)
}

const mapStateToProps = createStructuredSelector({
  currentUser : selectCurrentUser,
  currentList : selectCurrentList
});



export default connect(mapStateToProps)(CollectionItem);
