import {ListActionTypes} from './list.types';

const INITIAL_STATE = {
    currentList:[]
};

const listReducer = (state = INITIAL_STATE,action) => {
    switch(action.type) {
        case ListActionTypes.SET_CURRENT_LIST:
            console.log('try',action);
            return {
                ...state,
                currentList: action.payload
            }
        default:
            return state;
    }
};
export default listReducer;