import React from 'react';
import './public_list.styles.scss';
import {connect} from 'react-redux';
import {createStructuredSelector} from 'reselect';
import {getAll,firestore} from '../../firebase/firebase.utils';
import {setCurrentList} from '../../redux/list/list.actions';
import {selectCurrentList} from'../../redux/list/list.selector';
import CollectionItem from '../../components/collection-item/collection-item.component';
import Header from '../../components/header-component/header-component.jsx';
import Footer from '../../components/footer/footer.component.jsx';



class PublicList extends React.Component{
  constructor(props) {
    super(props);
    const {setCurrentList} = this.props;
}
    
  componentWillMount(){
    const {setCurrentList} = this.props;
    getAll().then( s=>{
      setCurrentList(s.lista);
     })
  }
  componentDidMount(){
    const {setCurrentList} = this.props;   

      var uid = "4hYmPE0ZvhPoHA0kYJaQu8KdrCs2"
      firestore.collection(`users/${uid}/lista/`)
      .onSnapshot(function(doc) {
        var source = doc.metadata.hasPendingWrites ? "Local" : "Server";
        console.log(source, " data2 : ", doc.docs.map( s=> s = s.data()));
        setCurrentList( doc.docs.map( s=> s = s.data()));
      });
  }


    render(){
      var lista = this.props.currentList; 

        // var data = this.state.list;
        // if(data && data.length>0 && data[0] && data[0].lista)
        // lista = data.lista;
        // else if(data.lista)lista=data.lista;
        return (     
          
          <div className="public_list"><Header /> 

            <div className="title">
            </div>
            <div className="collection">
              {/* <CustomButton success onClick={()=>UpdateList()} >Atualizar Lista</CustomButton> */}
                {
                   lista?
                    lista.map( (item) => (
                        <CollectionItem key={item.id} item={item} completed={item.completed? 'completed' : ''} />
                    )) 

                    :

                    <div className="no-list-div">
                   </div>                 
                }
              </div>
              <Footer />      
            </div> 
        );
           
    }
}
const mapStateToProps = createStructuredSelector({
  currentList: selectCurrentList,
});
const mapDispatchToProps = dispatch =>({
  setCurrentList: list => dispatch(setCurrentList(list))
}); 
export default connect(mapStateToProps,mapDispatchToProps)(PublicList);